// Templates

%home_project_template {
    width: 290px;
    height: 200px;
    color: white;
    border-radius: 20px;
    margin: 14px;
    border: 2px #777777 solid;
    font-size: 24px;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    position: relative;
}

%home_project_opacity {
    margin: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: rgba(0, 0, 0, 0.6);
    width: 100%;
    height: 100%;
    border-radius: 20px;
}

%home_project_text {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    margin: 0;
}

// Style

.home_projects_div {

    padding: 20px 0px 20px 0px;
    background-color: rgba(70, 61, 61, 0.6);

    .home_my_projects_title {
        font-size: 29px;
        font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif, 'Times New Roman', Times, serif;
        letter-spacing: 2px;
        padding: 20px 0px 20px 0px;
        text-align: center;
        color: whitesmoke;
        background-color: rgba(0, 0, 0, 0.2);
        user-select: none;
    }


    .home_projects_border {
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 10px;

        .link_to_pages_from_home {
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
            align-items: center;
            text-decoration: none;
            cursor: pointer;

            .home_project{
                @extend %home_project_template;
                .home_project_opacity{
                    @extend %home_project_opacity;
                    .home_project_text{
                        @extend %home_project_text;
                    }
                }
